.Home {
  position: relative;

  /* First block Section */

  .first-block-container {
    display: block;
    margin: -1%;
  }

  .first-block-container img {
    width: 100%;
    height: 100%;
  }


  /* Second block Section */

    .second-block-container {
      background: no-repeat bottom/cover url("../../assets/images/home/secondBlock_background.svg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      margin-top: -1%;
    }
  
    .wrapper-second-text {
      padding: 10% 5% 8% 12%;
    }
    
    .second-text {
      color: #2c2c2c !important;
      padding-bottom: 5%;
      font-size: 4rem;
      font-weight: 300;
      line-height: 4.5rem;
    }
  


  /* Third block Section */

  .third-block-container {
    background: linear-gradient(175.98deg, rgba(255, 240, 188, 0.234) 9.71%, rgba(255, 255, 255, 0) 184.99%);
    padding-bottom: 10%;
  }

  .wrapper-third-text {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 3%;
  }

  .third-text {
    margin: 9% 10% 11% -5%;
    font-size: 2.5rem;
    color: #2c2c2c;
    font-weight: normal;
    line-height: 2.8rem;
  }

  .third-text-center {
    margin: 9% 10% 11% 5%;
    font-size: 2.5rem;
    color: #2c2c2c;
    font-weight: normal;
    line-height: 2.8rem;
  }



 /* Download Products section*/

 .download-container {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  margin: 0% 2% !important;
}

.download-block {
  padding: 0 !important;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.description-download-text {
  padding-top: 20px;
}

.download-block-description {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.downnload-logo {
  flex: 0 0 25%;
  margin-right: 20px;
}

.download-about {
  flex: 0 1 80%;
  margin-left: 5%;
}

.logo-download-small {
  width: 100%;
  margin-right: 40px;
}

.download-button-block {
  text-align: center;
  margin-top: 20px;
}

.download-button {
  background-color: #3ED5F8;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  color: #142E50 !important;
  padding: 15px 80px;
  border: 0 !important;
  font-weight: 400;
  font-size: 1.5rem;
  text-align: center; 
}


  /* Feedback Section */

  .feedback-container {
    padding: 6% 0% 10% 0%;
    background-color: rgb(225, 248, 255);
  }

  .slick-list {
    margin: 0 7%;
  }

  .slick-slide .feedback-item {
    background-color: #fff !important;
    padding: 10px;
    z-index: -2;
    height: 250px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
    border-radius: 13px;
  }

  .slick-slide div {
    padding: 10px 15px 20px 15px;
    box-sizing: border-box;
    display: block;
    width: 100%;
    max-height: 350px !important;
   }

   .feedback-header {
    font-size: 3rem;
    text-align: center;
    color: #2c2c2c;
    padding-bottom: 4%;
   }

  .feedback-item h3 {
    padding-top: 10px;
    padding-left: 10px;
  }

  .feedback-item p {
    padding: 10px;
  }

  // .slick-next:before, .slick-prev:before {
  //   font-size: 35px;
  //   color: #F0BB04;
  // }

  // .slick-next {
  //   right: -10px;
  // }

  .slick-arrow {
    position: absolute;
    top: 35%;
    cursor: pointer;
  }

  .slick-arrow-next {
    right: -1px;
    position: absolute;
    top: 35%;
    cursor: pointer;
  }



  blockquote {
    font-weight: 100;
    font-size: 2rem;
    max-width: 500px;
    line-height: 1.2;
    position: relative;
    margin: 1;
    padding: 0.5rem;
  }
  
  blockquote:before,
  blockquote:after {
    position: absolute;
    color: rgba(240, 187, 4, 0.5);
    font-size: 8rem;
    width: 5rem;
    height: 5rem;
    font: {
      family: Georgia;
    }
  }
  
  blockquote:before {
    content: "\275D";
    left: -1rem;
    top: -2.5rem;
  }
  
  blockquote:after {
    content: "\275E";
    right: -2rem;
    top: 18rem;
    z-index: 1;
  }
  
  
  /* Blog Section */

  .blog-container {
    background: linear-gradient(177.45deg, rgba(199, 244, 255, 0.246) 9.71%, rgba(255, 255, 255, 0) 184.99%);
    padding: 7% 6%;
  }

  .blog-header {
    font-size: 3rem;
    padding-bottom: 30px;
    display: flex;
    justify-content: space-between;
  }

  .blog-button {
    padding-top: 20px;
    color: #34D086 !important;
    font-weight: 400;
    text-align: center; 
    font-size: 1.7rem;
    text-decoration: underline;
  }

  .blog-items {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .blog-item {
    width: 28%;
    height: 30%; 
  }

  .blog-item p {
    text-decoration: underline;
    color: #34D086 !important;
  }

  .blog-item img {
    width: 100%;
    height: 60%;
    padding-bottom: 5px;
  }



  /* Home Header Block */


  .hero {
    background: no-repeat bottom/cover url("../../assets/images/home/home_header.png");
    //background: no-repeat bottom/cover url("../../assets/images/home/bcg3.svg");
    text-align: center;
    margin-top: -100px;
    padding-top: 100px;
  }

  .wrapper-hero {
    padding: 8% 0 5%;
  }
  
  /*hero*/
  .hero-title {
    margin: 0 15% 0.5%;
    font-size: 2.3rem;
    font-weight: normal;
    line-height: 3.2rem;
    color: #fff;
  }

  .subtitle {
    color: #fff;
    font-size: 1.5rem;
    font-weight: 100;
    margin-bottom: 3rem;
    margin-top: 1%;
  }



  .button-started {
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 2%;
		&.bottom{
			margin: 0;
			transform: translateY(-50%);
		}
  }

  .get-started-button {
    display: block;
    width: auto;

    font-size: 1.4rem;
    text-transform: capitalize;
    color: #fff;

    background-color: #f26d6d;
    border: none;
    border-radius: 50px;
    transition: all 0.3s linear;
    &:hover {
      background-color: #ffb43f;
		}
		&.top,&.bottom{
			display: flex;
			align-items: center;
			padding: 0.5% 2%;
			.icon{
				padding-right: 3%;
				margin-bottom: 4%;
				flex: 0 0 auto;
				height: 3rem;
			}
			span{
				flex: 0 0 auto;
			}
		}
    &.bottom {
			font-size: 1.4rem;
    }
    &.blog-button {
			padding: 20px 20px;
			max-width: 360px;
    }
  }

  /*content*/
  .container {
    margin: 0 6%;
  }

  .content {
    margin: 6% 0;
    position: relative;
  }

  .enjoyIt-wrapper,
  .content-context-memory {
    display: flex;
    justify-content: space-between;
  }
  .content-context-memory {
    flex-direction: row-reverse;
    margin-top: -80px;
  }

  .enjoyIt-heading,
  .context-title {
    padding-top: 50px;
    flex: 0 1 50%;
  }

  .enjoyIt-heading {
    margin-right: 5%;
  }

  .context-title {
    margin-left: 5%;
  }

  .imgEnjoy img,
  .img-context-puzzle img {
    width: 100%;
    height: 100%;
  }

  .imgEnjoy,
  .img-context-puzzle {
    flex: 1 1 50%;
  }

  .app-download-wrapper {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  .img-phone {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 50%;
  }

  .img-phone .img-phone-front {
    position: absolute;
    width: 50%;
    left: -120%;
    transition: all 0.8s ease-in-out;
    &.show {
      left: 30%;
    }
  }
  .img-phone .img-phone-back {
    width: 100%;
  }

  .content-description {
    flex-basis: 50%;
    margin-left: 5%;
    text-align: left;
    h2 {
      font-size: 2.2rem;
    }
  }

  h2 > span {
    font-weight: 800;
  }

  h2 > sup {
    font-size: 1rem;
    font-weight: 800;
  }

  .openSoftware {
    color: lightblue;
    text-decoration: underline;
    transition: 0.3s;
  }

  .openSoftware:hover {
    text-decoration: none;
    color: #4d8bdd;
  }

  .button-block {
    margin-top: 3%;
  }

  .button-group {
    display: flex;
    width: 100%;
    margin-top: 4px;
    position: relative;
    align-items: center;
    &-comment {
      position: absolute;
			left: 100%;
      top: 5px;
      font-size: 0.9rem;
      color: #979797;
			text-align: left;
			width: fit-content;
			margin-left: 5px;
			font-weight: 100;
    }
  }

  .button-group-1 {
    margin-left: 4%;
  }

  .button-group-1 .button-install {
    background-color: #56d3f1;
  }

  .button-group-2 {
    margin-left: 2%;
  }

  .button-group-2 .button-install {
    background-color: #46b8ff;
  }

  .button-group-3 .button-install {
    background-color: #5396ef;
  }

  .button-install {
		padding: 20px 20px;
    flex: 0 0 55%;
    background-color: #56d3f1;
    border-radius: 50px;
    display: block;
		transition: all 0.2s ease-in-out;
		text-align: center;
		white-space: nowrap;
    &:hover {
      transform: scale(1.1);
    }
    &.disabled {
			background-color: #c4c4c4;
			pointer-events: none;
      cursor: default;
      &:hover {
        transform: scale(1);
      }
    }

    background-color: #2c2c2c;
	}
	
	.button-chrome{
		&-wrapper{
			position: relative;
			width: 100%;
			padding-left: 3.2rem;
		}
		&-icon{
			position: absolute;
			top: 30%;
			transform: translateY(-50%);
			left: 0;
			height: 3rem;
		}
	}

  .button-android,
  .button-chrome {
    border-right: 4px solid white;
    border-bottom: 4px solid white;
    border-top: 4px solid white;
    z-index: 2;
	}
	
  .button-guide,
  .button-iOS {
    position: relative;
    padding: 16px 5px;
  }

  .button-iOS {
		position: relative;
    right: 30%;
    text-align: center;
    padding-left: 29%;
		padding-right: 1%;
		flex: 0 0 50%;
  }

  .button-guide {
    font-size: 0.7rem;
    opacity: 0;
    right: 8%;
    padding-left: 30px;
  }

  .button-group-1:hover .button-guide {
    opacity: 1;
  }

  .content-howItWorks h2 {
    text-align: center;
    margin-bottom: 2%;
  }

  .extension img {
    width: 100%;
	}
	
	.content-sources{
		margin-bottom: 1%;
		padding-bottom: 0;
	}

  .sources-wrapper {
    justify-content: center;
    text-align: left;
    display: grid;
    grid-template-areas:
      "text stickers"
      "button stickers";
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: 1fr auto;
  }

  .sources-title {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 20px 40px 20px 50px;
    grid-area: text;
    h2 {
      margin-bottom: 0.5rem;
    }
  }
  .blog {
    padding: 20px 40px 0 50px;
    grid-area: button;
    p {
      margin-bottom: 1.5rem;
    }
  }
  .sources-block {
		grid-area: stickers;
		margin:-2%;
		&_row{
			width: 100%;
			display: flex;
		}
		.sources-paragraph {
			flex: 0 1 50%;
			margin: 2%;
			padding: 20px;
			border-radius: 25px;
			background: #45b8ff;
			color: white;
			p {
				font-size: 1rem;
				color: white;
			}
			&.disabled{
				opacity: 0;
			}
		}
  }


  .get-started-footer {
		background: url("../../assets/images/home/home_footer.svg") bottom/cover no-repeat;
    height: 16vw;
    margin-top: -5%;
  }
}
